import request from "../utils/request";

export function login(username, password) {

  return request({
    url: "/login",
    method: "post",
    data: {
      username, password
    }
  })

}


export function logout() {

  return request({
    url: "/logout",
    method: "get",
  })

}


export function getOperatorInfo(uid) {
  return request({
    url: "/getUserInfo",
    method: "get",
    params: {
      uid
    }
  })

}


export function getOperatorList(currentPage, pageSize, sortBy = "", sort = "", status = '', searchBy = "", searchValue = "", startTime = "", endTime = "") {
  return request({
    url: "/Operator",
    method: "get",
    params: {
      searchBy,
      searchValue,
      startTime,
      endTime,
      sortBy,
      sort,
      status,
      currentPage,
      pageSize,
    }
  })
}

export function getOperatorPermission() {
  return request({
    url: "/getMenuPermission",
    method: "get"
  })

}

export function getMenuPermission() {
  return request({
    url: "/getMenuPermission",
    method: "get"
  })
}

export function setMenuPermission(data) {
  return request({
    url: "/setMenuPermission",
    method: "post",
    data: {data}
  })

}

export function getAPIPermissionByMenu(menu) {
  return request({
    url: "/getAPIPermissionByMenu",
    method: "get",
    params: {
      menu
    }
  })

}

export function setAPIPermissionByMenu(data) {
  return request({
    url: "/setAPIPermissionByMenu",
    method: "post",
    data: {
      data
    }
  })

}


export function deleteAPIPermissionByMenu(ap_id) {
  return request({
    url: "/deleteAPIPermissionByMenu",
    method: "delete",
    params: {
      ap_id
    },
  })

}



